<template>
  <div class="guestbook">
    <h1 :style="styleObject">Gästbok</h1>

    <b-form-input v-model="text" placeholder="För & efternamn"> </b-form-input>

    <b-button @click="onClick(initials)" block variant="success"
      >Lägg till</b-button
    >
    <p></p>
    <b-list-group>
      <b-list-group-item v-for="guest in guestbook" :key="guest.name"
        >( {{ guest.initials }} ), {{ guest.name }}
      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>
export default {
  created() {},
  computed: {
    initials() {
      return this.text
        .split(" ")
        .map((text) => text.charAt(0))
        .join(".");
    },
  },
  data() {
    return {
      text: "",
      guestbook: [],
      styleObject: {
        color: "#42b983",
        fontSize: "50px",
      },
    };
  },
  watch: {
    text(val) {
      for (let index = 0; index < this.guestbook.length; index++) {
        if (val === this.guestbook[index].name) {
          alert(`Du har redan skrivit ${val}`);
          console.log(`Du har redan skrivit ${val}`);
        }
      }
    },
  },
  name: "Guestbook",
  methods: {
    onClick(n) {
      this.guestbook.push({ name: this.text, initials: n });
      this.text = "";
    },
  },
};
</script>

